import React, { Component } from 'react';
import TypeAnimation from 'react-type-animation';
import TimedComponent from "./TimedComponent";
import $ from 'jquery';

const LyricsInput = props => (
    <label>
        <p><strong>Your lyric:</strong></p>
        <input
            type="text"
            onChange={props.handleChange}
            value={props.value}
        />
    </label>
)

export default class TaylorizerOnboarder extends Component {

    state = {
        renderView: 0,
        startingLyric: "",
        returnedLyrics: "",
        skipped: false
    }

    clickBtn = e => {
        this.setState({
            renderView: this.state.renderView += 1
        });
    }

    skip = e => {
        this.setState({
            renderView: 3,
            skipped: true
        });
    }

    handleChange = e => {
        this.setState({
            startingLyric: e.target.value
        });
    }

    taylorize = () => {

        if (this.state.skipped) {
            this.setState({
                renderView: 5
            });
        } else {
            this.setState({
                renderView: 4
            });
        }

        var apiUrl = "https://gpt2-nkxmmqpf7q-uw.a.run.app?length=115&temperature=1.2&top_p=0.9";
        var encodedLyric = this.state.startingLyric;
        let displayFunc = this.displayLyrics;
        let truncate = this.truncate;

        if (this.state.startingLyric != null) {
            encodedLyric = this.encode(this.state.startingLyric);
            apiUrl += "&prefix=" + encodedLyric;
        }

        $.ajax({
            url: apiUrl,
            type:'GET',
            dataType: 'json',
            success: function(data) {
                displayFunc(truncate(data.text));
            }
        });

    }

    displayLyrics = (data) => {
        this.setState({
            returnedLyrics: data,
            renderView: 6
        })
    }

   truncate = (data) => {
        if(data.lastIndexOf("\n")>0) {
            return data.substring(0, data.lastIndexOf("\n"));
        } else {
            return data;
        }
    }

    encode = (lyric) => {
        return lyric.replace(new RegExp(" ", "g"), "%20");
    }

    render () {
        switch(this.state.renderView) {
            case 1:
                return <div>
                    <img alt="A cut out of taylor swift's head" src={require("./taylor-head.png")} width="100px" height="100px" />
                    <TypeAnimation
                        cursor={false}
                        sequence={["I'm the Taylorizer.", 1000, "I can write Taylor Swift songs.", 2000]}
                        wrapper="p"
                        className="type"
                    />
                    <TimedComponent
                        element={
                            <div>
                                <button onClick={this.clickBtn}>Prove it.</button>
                                <button onClick={this.skip}>I know. I've done this before.</button>
                            </div>
                        }
                        timeout="5500"
                    />
                </div>
            case 2:
                return <div>
                    <img alt="A cut out of taylor swift's head" src={require("./taylor-head.png")} width="100px" height="100px" />
                    <TypeAnimation
                        cursor={false}
                        sequence={["You don't believe me?", 1000, "I need some ✨inspiration✨.", 1000, "You write the first lyric, and I'll handle the rest.", 1000]}
                        wrapper="p"
                        className="type"
                    />
                    <TimedComponent />
                    <TimedComponent
                        element={
                            <div>
                                <br />
                                <button onClick={this.clickBtn}>OK, Fine</button>
                            </div>
                        }
                        timeout="13500"
                    />
                </div>
            case 3:
                return <div>
                    <div>
                        <div>
                            <img alt="A cut out of taylor swift's head" src={require("./taylor-head.png")} width="100px" height="100px" />
                            <LyricsInput handleChange={this.handleChange} value={this.state.startingLyric} />
                        </div>
                        <br />
                        <button onClick={this.taylorize}>Taylorize!</button>
                    </div>
                </div>
            case 4:
                return <div>
                    <img alt="A cut out of taylor swift's head" src={require("./taylor-head.png")} width="100px" height="100px" />
                    <TypeAnimation
                        cursor={false}
                        sequence={["Really? THAT'S all you could come up with?", 2000, "Oh well. Now Taylorizing...", 2000]}
                        wrapper="p"
                        className="type"
                    />
                </div>
            case 5:
                return <div>
                    <img alt="A cut out of taylor swift's head" src={require("./taylor-head.png")} width="100px" height="100px" />
                    <TypeAnimation
                        cursor={false}
                        sequence={["You know, you didn't have to be so sassy back there.", 2000, "Now Taylorizing...", 2000]}
                        wrapper="p"
                        className="type"
                    />
                </div>
            case 6:
                return <div>
                    <img alt="A cut out of taylor swift's head" src={require("./taylor-head.png")} width="100px" height="100px" />
                    <TypeAnimation
                        cursor={false}
                        sequence={["Our new song is ready.", 2000]}
                        wrapper="p"
                        className="type"
                    />
                    <TimedComponent />
                    <TimedComponent
                        element={
                            <div>
                                <p className="break">
                                    <strong></strong>
                                    {this.state.returnedLyrics}
                                </p>
                                <p className="gray">
                                    Want a different song? Sometimes I like to copy the ones Taylor already wrote.
                                    <br />
                                    Refresh the page and try again.
                                </p>
                            </div>
                        }
                        timeout="6000"
                    />
                </div>
            default:
                return <div>
                    <img alt="A cut out of taylor swift's head" src={require("./taylor-head.png")} width="100px" height="100px" />
                    <p>
                        Generate Taylor Swift lyrics from an AI using GPT-2
                    </p>
                    <button onClick={this.clickBtn}>Get Started</button>
                </div>
        }
    }
}